import { Suite, Test } from "@bcwdev/vue-api-tester";

const PATH = "//localhost:3000/api/bugs";
let bugObject = {
  closed: false,
  _id: "",
  title: "Can't Eat Tacos",
  description: "Can't eat tacos because I don't have any.",
  reportedBy: "D$",
  createdAt: "2019-12-11T20:13:10.079Z",
  updatedAt: "2019-12-11T20:13:10.079Z",
  __v: 0
};
export class BugsSuite extends Suite {
  constructor() {
    super("BugsController", PATH);
    this.newBug = {
      title: "Can't Eat Tacos",
      description: "Can't eat tacos because I don't have any.",
      reportedBy: "D$"
    };
    this.setCleanup(async () => {
      await this.get("//localhost:3000/cleanup");
    });
    this.addTests(
      new Test(
        {
          name: "Can Get all Bugs",
          path: PATH,
          description: "GET request. This should get a list of bug objects.",
          expected: "[]"
        },
        async () => {
          let result;
          try {
            result = await this.get();
            return this.pass("Able to get bugs", result);
          } catch (e) {
            return this.handleError(e);
          }
        }
      ),
      new Test(
        {
          name: "Can Create Bugs",
          path: PATH,
          description:
            "POST request. This should create a new bug in your database.",
          expected: "bug object",
          payload: "bug object"
        },
        async () => {
          let created;
          try {
            created = await this.create(this.newBug);
          } catch (e) {
            return this.handleError(e);
          }
          return this.pass("Successfully created bug", created);
        },
        async () => {
          await this._cleanup();
        }
      ),
      new Test(
        {
          name: "Can Get bug by bug Id",
          path: PATH + "/:id",
          description: "GET request. This should get one bug by its id.",
          expected: "bug object"
        },
        async () => {
          let created;
          let result;
          try {
            created = await this.create(this.newBug);
            result = await this.getById(created._id);
          } catch (e) {
            return this.handleError(e);
          }
          let invalidCheck = checkForMissingProperties(result);
          if (invalidCheck) {
            return this.unexpected(bugObject, result, invalidCheck);
          }
          return this.pass("We successfully got a bug by its Id!", result);
        },
        async () => {
          await this._cleanup();
        }
      ),
      new Test(
        {
          name: "Can Edit bug by bug Id",
          path: PATH + "/:id",
          description: "PUT request. This should update one bug by its Id.",
          expected: "bug object",
          payload: "bug object { title: string, description: string }"
        },
        async () => {
          let created;
          let updated;
          try {
            created = await this.create(this.newBug);
            created.id = created._id;
            created.description = "This one has been edited.";
            await this.update(created);
            updated = await this.getById(created._id);
          } catch (e) {
            return this.handleError(e);
          }
          return this.pass("Successfully edited a bug!", created);
        },
        async () => {
          await this._cleanup();
        }
      ),
      new Test(
        {
          name: "Can close bug by bug Id",
          path: PATH + "/:id",
          description: "DELETE request. This should close one bug by its Id.",
          expected: "string"
        },
        async () => {
          let created;
          let deleted;
          let result;

          try {
            created = await this.create(this.newBug);
            result = await this.delete(created._id);
            deleted = await this.getById(created._id);
            let invalidCheck = checkForMissingProperties(deleted);
            if (!deleted.closed) {
              return this.fail("Oops, looks like this bug is still open.");
            } else if (invalidCheck) {
              return this.unexpected(
                { ...deleted, closed: true },
                deleted,
                invalidCheck
              );
            }
          } catch (e) {
            return this.handleError(e);
          }
          return this.pass("We successfully closed a bug by its Id!", result);
        },
        async () => {
          await this._cleanup();
        }
      ),
      new Test(
        {
          name: "Cannot Edit closed bug",
          path: PATH + "/:id",
          description:
            "PUT request. This should not allow a closed bug to be updated.",
          expected: "bug object",
          payload: "bug object { title: string, description: string }"
        },
        async () => {
          let bugToEdit;
          let bugNotToEdit;
          let updatedBugToEdit = {};
          let updatedBugNotToEdit = {};
          try {
            bugToEdit = await this.create(this.newBug);
            bugNotToEdit = await this.create(this.newBug);
            await this.delete(bugNotToEdit._id);
            bugNotToEdit = await this.getById(bugNotToEdit._id);
            updatedBugToEdit.id = bugToEdit._id;
            updatedBugNotToEdit.id = bugNotToEdit._id;
            updatedBugToEdit.description = "This one has been edited.";
            updatedBugNotToEdit.description = "This one has been edited.";
            await this.update(updatedBugToEdit);
            updatedBugToEdit = await this.getById(updatedBugToEdit.id);
          } catch (e) {
            return this.handleError(e);
          }
          try {
            await this.update(updatedBugNotToEdit);
            updatedBugNotToEdit = await this.getById(bugNotToEdit._id);
          } catch (e) {
            updatedBugNotToEdit = await this.getById(bugNotToEdit._id);
            if (
              e.response.status == 400 &&
              updatedBugToEdit.description != bugToEdit.description
            ) {
              return this.pass("Cannot edit a bug!", updatedBugNotToEdit);
            }
          }
          if (updatedBugNotToEdit.description == bugNotToEdit.description) {
            return this.pass(
              "Cannot edit a bug! The bug was not editable, but no error was caught. Your server should throw an error if the update does not succeed.",
              updatedBugNotToEdit
            );
          }
          if (bugNotToEdit.description != updatedBugNotToEdit.description) {
            return this.unexpected(bugNotToEdit, updatedBugNotToEdit);
          }
          return this.fail("oops something went wrong");
        },
        async () => {
          await this._cleanup();
        }
      )
    );
  }
  handleError(e) {
    if (e.response.status == 404) {
      return this.fail(
        `${
          e.response.data
        } 404: ${e.response.config.method.toUpperCase()} request to ${
          e.response.config.url
        }`
      );
    }
    return this.unexpected(this.newBug, e.response.data.error.message);
  }
}

function checkForMissingProperties(o) {
  let error = "";
  Object.keys(bugObject).forEach(k => {
    if (!o.hasOwnProperty(k)) {
      error += `${k}, `;
    }
  });
  if (error.length) {
    error = "[ERROR] - Missing Properties: " + error.slice(0, -2);
  }
  return error;
}
