import { Suite, Test } from "@bcwdev/vue-api-tester"

const PATH = "//localhost:3000/api/notes"

let noteObject = {
  "_id": "5df177587b30f817404c5094",
  "content": "This is a note.",
  "reportedBy": "D$",
  "bug": "5df177547b30f817404c5093",
  "createdAt": "2019-12-11T23:10:16.919Z",
  "updatedAt": "2019-12-11T23:10:16.919Z",
  "__v": 0
}

export class NotesSuite extends Suite {
  constructor() {
    super("NotesController", PATH)
    this.newBug = { title: "Can't Eat Tacos", description: "Can't eat tacos because I don't have any.", reportedBy: "D$" }
    this.newNote = { title: "Note", content: "This is a note.", reportedBy: "D$" }
    this.setCleanup(async () => { await this.get('//localhost:3000/cleanup') })
    this.addTests(
      new Test({
        name: "Can Get all Notes for a Bug",
        path: '//localhost:3000/api/bugs/:bugId/notes',
        description: 'GET request. This should get a list of note objects belonging to a single Bug.',
        expected: "[]"
      },
        async () => {
          let result
          try {
            let bug = await this.create(this.newBug, '//localhost:3000/api/bugs')
            result = await this.get(`//localhost:3000/api/bugs/${bug._id}/notes`)
          } catch (e) {
            return this.handleError(e)
          }
          return this.pass("Able to get notes", result)
        },
        async () => {
          await this._cleanup()
        }
      ),
      new Test({
        name: 'Can Create Notes',
        path: PATH,
        description: 'POST request. This should create a new note in your database.',
        expected: 'note object',
        payload: 'note object'
      },
        async () => {
          let bug
          let created
          try {
            bug = await this.create(this.newBug, '//localhost:3000/api/bugs')
            this.newNote.bug = bug._id
            created = await this.create(this.newNote)
          } catch (e) {
            return this.handleError(e)
          }
          if (!validateIsNote(created)) {
            return this.unexpected(noteObject, created)
          }
          return this.pass("Successfully created note ", created)
        },
        async () => {
          await this._cleanup()
        }
      ),
      new Test({
        name: 'Can delete note by note Id',
        path: PATH + '/:id',
        description: 'DELETE request. This should delete one note by its Id.',
        expected: 'string'
      },
        async () => {
          let bug
          let created
          let result
          let deleted
          try {
            bug = await this.create(this.newBug, '//localhost:3000/api/bugs')
            this.newNote.bug = bug._id
            created = await this.create(this.newNote)
            result = await this.delete(created._id)
          } catch (e) {
            return this.handleError(e)
          }
          try {
            deleted = await this.getById(created._id)
            return this.fail("Oops, looks like this note still exists.")
          } catch (e) {
            return this.pass("We successfully closed a bug by its Id!", result)
          }
        },
        async () => {
          await this._cleanup()
        }
      )
    )
  }
  handleError(e) {
    if (e.response.status == 404) {
      return this.fail(`${e.response.data} 404: ${(e.response.config.method).toUpperCase()} request to ${e.response.config.url}`)
    }
    return this.unexpected(this.newBug, e.response.data.error.message)
  }
}


function validateIsNote(o) {
  return Object.keys(noteObject).every(k => o.hasOwnProperty(k))
}
